import React from 'react';
import cn from 'classnames';
import { format } from 'date-fns';
import { MonthListMonthProps } from 'shared/types/calendar';

import styles from './styles.scss';

const Month: React.FC<MonthListMonthProps> = ({
  date,
  isToday,
  isActive,
  locale,
  onMonthClick
}) => {
  const handleClick = (): void => {
    onMonthClick(date);
  };

  return (
    <div
      className={ cn(styles.month, {
        [styles['month--today']]: isToday,
        [styles['month--active']]: isActive
      }) }
      onClick={ handleClick }
      role="presentation"
    >
      <span>{ format(date, 'LLLL', { locale }) }</span>
    </div>
  );
};

export default Month;
